.app {
  font-family: 'Inter', sans-serif;
  color: #333;
}

main {
  padding: 1rem;
}

h1 {
  text-align: center;
  margin-bottom: 1rem;
}

.campaigns-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
  justify-content: center;
}
